.App {
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.canvas {
  background-color: #ffffff;
  box-shadow: 0px 0px 16px rgba(0,0,0,0.4);
  border-radius: 4px;
}

.info {
  padding: 12px;
  background-color: #fff09d;
  margin: 12px;
  border-radius: 8px;
  color: #404040;
}
