.dialog-background {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: rgba(0,0,0,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
}

.dialog-content {
    padding: 16px;
    margin: 24px;
    border-radius: 8px;
    background-color: #ffffff;
}

.dialog-image {
    width: 96px;
    height: 96px;
}

.dialog-image-container {
    width: 96px;
    height: 96px;
    background-color: rgb(186, 215, 238);
    border-radius: 6px;
    overflow: hidden;
}

.dialog-title {
    font-size: 20px;
    color: #202020;
    width: 100;
    text-align: center;
}

.dialog-main-container {
    display: flex;
    align-items: center;
    margin: 24px;
    font-size: 36px;
    color: #202020;
}