.button-container {
    padding: 12px;
    padding-left: 24px;
    padding-right: 24px;
    display: inline-block;
    font-size: 18px;
    color: #ffffff;
    background-color: rgb(43, 100, 146);
    border-radius: 8px;
    cursor: pointer;
}

.button-container:hover {
    background-color: rgb(64, 124, 173);
}