.loading-container{
    width: 32px;
    height: 32px;
}

.loading-indicator {
    width: 28px;
    height: 28px;
    fill: rgb(43, 100, 146);
    animation: spin 1.2s infinite linear;
}

@keyframes spin {
    0% {
        transform:rotate(0deg);
    }
    100% {
        transform:rotate(360deg);
    }
}